@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
html {
	font-size: 10px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

body {
	margin: 0;
	min-width: 320px;
	background: white;
	color: #575c74;
	font: 400 1.6rem/1.8 "Open Sans", sans-serif;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}
body.overflow-disabled, body.menu-opened {
	overflow: hidden;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	text-decoration: underline;
}

input {
	outline: none;
}

button,
input[type=search],
input[type=button],
input[type=reset],
input[type=submit] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

#main {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

p {
	margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
	margin-top: 0;
	color: #0c122d;
}

.container {
	max-width: 115.5rem;
	width: 100%;
	margin: auto;
	padding-left: 2rem;
	padding-right: 2rem;
}

.h1 {
	font-family: "Rubik", sans-serif;
	font-weight: 700;
	font-size: 7.2rem;
	line-height: 1.13;
}
@media (max-width: 991px) {
	.h1 {
		font-size: 5.2rem;
	}
}
@media (max-width: 767px) {
	.h1 {
		font-size: 4.2rem;
	}
}

.h2 {
	margin-bottom: 2.6rem;
	font-family: "Rubik", sans-serif;
	font-weight: 700;
	font-size: 4.8rem;
	line-height: 1.04;
}
@media (max-width: 991px) {
	.h2 {
		font-size: 3.8rem;
	}
}
@media (max-width: 767px) {
	.h2 {
		font-size: 3rem;
	}
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -1.5rem;
	margin-right: -1.5rem;
}
.row .col {
	padding-left: 1.5rem;
	padding-right: 1.5rem;
}

.sr-only {
	position: absolute !important;
	width: 1px !important;
	height: 1px !important;
	padding: 0 !important;
	margin: -1px !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	white-space: nowrap !important;
	border: 0 !important;
}

.border-radius-type-1 {
	border-radius: 1.6rem 1.6rem 1.6rem 0px;
}

.green-section-holder {
	background: #f5fcfa;
}

.illustration-block {
	position: absolute;
	width: 100rem;
	height: 140rem;
	z-index: 1;
}
.illustration-block::before {
	content: "";
	display: block;
	width: 100%;
	height: 100%;
	background-size: contain;
	background-repeat: no-repeat;
}
.illustration-block.blue::before {
	background-image: url("data:image/svg+xml,%3Csvg width='756' height='1462' viewBox='0 0 756 1462' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath style='mix-blend-mode:multiply' d='M-314.736 419.548C-345.978 388.306 -345.978 337.653 -314.736 306.411L-31.8932 23.5685C-0.651306 -7.67339 50.0019 -7.67339 81.2439 23.5685L731.782 674.107C763.024 705.349 763.024 756.002 731.782 787.244L81.2439 1437.78C50.0019 1469.02 -0.651306 1469.02 -31.8932 1437.78L-314.736 1154.94C-345.978 1123.7 -345.978 1073.04 -314.736 1041.8L-60.1775 787.244C-28.9356 756.002 -28.9356 705.349 -60.1775 674.107L-314.736 419.548Z' fill='url(%23paint0_linear_5_59)'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_5_59' x1='-357.162' y1='348.838' x2='406.513' y2='1112.51' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%231B5FC1' stop-opacity='0.08'/%3E%3Cstop offset='1' stop-color='%231B5FC1' stop-opacity='0.04'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
}
.illustration-block.left-pos-1 {
	top: 100rem;
	left: 0;
}

/* header */
#header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 2rem;
	padding-bottom: 2rem;
	z-index: 100;
}
@media (max-width: 767px) {
	#header {
		padding-top: 1.4rem;
		padding-bottom: 1.4rem;
	}
}
#header .container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
#header .logo {
	display: inline-flex;
	width: 100%;
	max-width: 14rem;
}
@media (min-width: 992px) {
	#header .logo {
		margin-right: 6.1rem;
	}
}
@media (max-width: 767px) {
	#header .logo {
		max-width: 8.9rem;
	}
}
#header .logo img {
	width: 100%;
	height: auto;
}
#header .nav {
	flex: 1;
	transition: all 0.3s ease;
}
@media (max-width: 991px) {
	#header .nav {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		background: white;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
}
@media (min-width: 992px) {
	#header .nav .inner-holder {
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
}
@media (max-width: 991px) {
	#header .nav .inner-holder {
		overflow: auto;
		width: 100%;
		padding: 3rem 0;
	}
}
#header .nav .menu {
	margin: 0;
	padding: 0;
	list-style: none;
	font-weight: 500;
	font-size: 1.8rem;
	color: #7d7d7d;
}
@media (min-width: 992px) {
	#header .nav .menu {
		display: flex;
		flex-wrap: wrap;
	}
}
@media (max-width: 991px) {
	#header .nav .menu {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
@media (min-width: 992px) {
	#header .nav .menu .menu-item {
		margin-right: 4.5rem;
	}
}
@media (max-width: 991px) {
	#header .nav .menu .menu-item {
		margin-bottom: 3rem;
	}
}
#header .nav .menu .menu-item a.active {
	color: #0c122d;
}
@media (max-width: 991px) {
	.menu-opened #header .nav {
		opacity: 1;
		visibility: visible;
		pointer-events: all;
	}
}
#header .btn-holder {
	display: flex;
}
@media (max-width: 991px) {
	#header .btn-holder {
		justify-content: center;
	}
}
#header .open-menu {
	position: relative;
	width: 2.2rem;
	height: 2.2rem;
	display: block;
	font-size: 0;
	line-height: 0;
}
@media (min-width: 992px) {
	#header .open-menu {
		display: none;
	}
}
#header .open-menu span {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	background: #575c74;
	border-radius: 0.2rem;
	height: 0.2rem;
	transition: all 0.3s ease;
}
#header .open-menu span:nth-child(1) {
	margin-top: -0.7rem;
}
#header .open-menu span:nth-child(2) {
	margin-top: -0.1rem;
}
#header .open-menu span:nth-child(3) {
	margin-top: 0.5rem;
}
.menu-opened #header .open-menu span:nth-child(1) {
	margin-top: 0;
	transform: rotate(45deg);
}
.menu-opened #header .open-menu span:nth-child(2) {
	opacity: 0;
	transform: translateX(50%);
}
.menu-opened #header .open-menu span:nth-child(3) {
	margin-top: 0;
	transform: rotate(-45deg);
}

.btn {
	display: inline-flex;
	justify-content: center;
	padding: 1.1rem 2.7rem;
	font-family: "Rubik", sans-serif;
	font-weight: 500;
	font-size: 1.4rem;
	border-radius: 8px;
}
.btn:hover {
	text-decoration: none;
}
.btn-primary {
	color: white;
	background: #1b5fc1;
	border: solid 0.1rem #1b5fc1;
}
.btn-primary:hover {
	background: #2d77e2;
	border-color: #2d77e2;
}
.btn-outline-warning {
	color: #ffb800;
	border: solid 0.1rem #ffb800;
}
.btn-outline-warning:hover {
	color: white;
	background: #ffb800;
}
.btn-outline-primary {
	color: #1b5fc1;
	border: solid 0.1rem #1b5fc1;
}
.btn-outline-primary:hover {
	color: white;
	background: #1b5fc1;
}

.intro-section {
	padding-top: 15.3rem;
	overflow: hidden;
}
@media (max-width: 991px) {
	.intro-section {
		padding-top: 10.3rem;
	}
}
@media (max-width: 767px) {
	.intro-section {
		position: relative;
		padding-top: 7rem;
		padding-bottom: 3rem;
	}
}
@media (min-width: 768px) {
	.intro-section .container {
		position: relative;
	}
}
.intro-section .content-holder {
	position: relative;
	max-width: 55.5rem;
	z-index: 2;
}
@media (max-width: 767px) {
	.intro-section .content-holder .before-title,
	.intro-section .content-holder .h1 {
		display: none;
	}
}
.intro-section .before-title {
	display: block;
	margin-bottom: 3rem;
	color: #ffb800;
	font-size: 1.4rem;
}
@media (max-width: 767px) {
	.intro-section .before-title {
		margin-bottom: 1.5rem;
	}
}
.intro-section .h1 {
	margin-bottom: 4rem;
}
@media (max-width: 767px) {
	.intro-section .h1 {
		margin-bottom: 2rem;
	}
}
.intro-section .text-holder {
	margin-bottom: 5.2rem;
	font-size: 1.6rem;
	line-height: 1.5;
}
@media (max-width: 767px) {
	.intro-section .text-holder {
		margin-bottom: 2rem;
	}
}
.intro-section .btns-holder {
	display: flex;
}
.intro-section .btns-holder .btn:first-child {
	margin-right: 2rem;
}
.intro-section .btn-primary {
	box-shadow: 0 1rem 1.5rem rgba(27, 95, 193, 0.19);
}
.intro-section .background-img {
	position: absolute;
	top: -15.3rem;
	right: -35rem;
	opacity: 0.15;
	width: 94rem;
	height: 91.4rem;
}
@media (max-width: 767px) {
	.intro-section .background-img {
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.intro-section .background-img img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-radius: 0 0 0 15rem;
}
.intro-section .img-holder {
	position: relative;
	display: flex;
	max-width: 49.6rem;
	z-index: 2;
}
@media (min-width: 768px) {
	.intro-section .img-holder {
		margin-left: auto;
	}
}
@media (max-width: 767px) {
	.intro-section .img-holder {
		margin: auto auto 2rem auto;
	}
}
.intro-section .img-holder img {
	width: 100%;
	height: auto;
}
@media (min-width: 768px) {
	.intro-section .row {
		align-items: center;
	}
}
@media (max-width: 767px) {
	.intro-section .row {
		flex-direction: column-reverse;
	}
}
.intro-section .row .col {
	width: 100%;
}
@media (min-width: 768px) {
	.intro-section .row .col {
		max-width: 100%;
		flex: 1 0 0%;
	}
}
@media (min-width: 768px) {
	.intro-section .tablet-content {
		display: none;
	}
}
@media (max-width: 767px) {
	.intro-section .tablet-content {
		display: block;
	}
}

.futures-section {
	padding-top: 7.2rem;
	padding-bottom: 4.3rem;
}
@media (min-width: 992px) {
	.futures-section {
		padding-bottom: 4.3rem;
	}
}
@media (max-width: 767px) {
	.futures-section {
		padding-bottom: 2.2rem;
	}
}
.futures-section .row .col {
	flex: 0 0 auto;
	width: 25%;
}
@media (max-width: 991px) {
	.futures-section .row .col {
		width: 33.33333333%;
	}
}
@media (max-width: 767px) {
	.futures-section .row .col {
		width: 50%;
	}
}
@media (max-width: 575px) {
	.futures-section .row .col {
		width: 100%;
	}
}
.futures-section .futures-item {
	display: flex;
}
@media (max-width: 991px) {
	.futures-section .futures-item {
		margin-bottom: 3rem;
	}
}
@media (max-width: 767px) {
	.futures-section .futures-item {
		margin-bottom: 2rem;
	}
}
.futures-section .futures-item .icon-holder {
	min-width: 4rem;
	width: 4rem;
	margin-right: 2rem;
}
@media (max-width: 767px) {
	.futures-section .futures-item .icon-holder {
		min-width: 3rem;
		width: 3rem;
		margin-right: 1.5rem;
	}
}
.futures-section .futures-item .icon-holder img {
	width: 100%;
	height: auto;
}
.futures-section .futures-item .h3 {
	margin-bottom: 1.5rem;
	font-weight: 700;
	font-size: 2rem;
	line-height: 1.4;
}
@media (max-width: 767px) {
	.futures-section .futures-item .h3 {
		margin-bottom: 1rem;
		font-size: 1.8rem;
	}
}
.futures-section .futures-item .text-holder {
	font-size: 1.5rem;
	line-height: 1.46;
}

.articles-section {
	position: relative;
	padding-top: 8rem;
	overflow: hidden;
	z-index: 2;
}
@media (max-width: 767px) {
	.articles-section {
		padding-top: 5rem;
	}
}
.articles-section .row {
	margin-left: -2.35rem;
	margin-right: -2.35rem;
}
.articles-section .row .col {
	padding-left: 2.35rem;
	padding-right: 2.35rem;
}
.articles-section .row .col {
	width: 100%;
}
@media (min-width: 768px) {
	.articles-section .row .col {
		max-width: 100%;
		flex: 1 0 0%;
	}
}
.articles-section .articles-item {
	margin-bottom: 8rem;
}
@media (max-width: 767px) {
	.articles-section .articles-item {
		margin-bottom: 4rem;
	}
}
.articles-section .articles-item:last-child .row {
	flex-direction: row-reverse;
}
.articles-section .articles-item .img-holder {
	display: flex;
}
@media (max-width: 767px) {
	.articles-section .articles-item .img-holder {
		margin-bottom: 2rem;
	}
}
.articles-section .articles-item .img-holder img {
	width: 100%;
	height: auto;
}
@media (max-width: 767px) {
	.articles-section .articles-item .h2 {
		margin-bottom: 1rem;
	}
}

.section-mosaic {
	padding-bottom: 3rem;
}
.section-mosaic .mosaic-holder {
	display: flex;
	min-height: 60rem;
	border-radius: 0 2rem 2rem 0;
	overflow: hidden;
}
.section-mosaic .left-side,
.section-mosaic .right-side {
	width: 100%;
}
.section-mosaic .left-side {
	position: relative;
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
}
.section-mosaic .left-side .img-holder {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 90%;
}
.section-mosaic .left-side .img-holder img {
	width: 100%;
	height: 100%;
}
.section-mosaic .left-side .mosaic-block {
	position: relative;
	z-index: 2;
	padding: 6rem 3rem 5rem 3rem;
}
.section-mosaic .right-side .two-cols {
	display: flex;
}
.section-mosaic .right-side .mosaic-block {
	padding: 8rem 3rem 13rem 3rem;
}
.section-mosaic .right-side .text-holder {
	margin-bottom: 2.8rem;
}
.section-mosaic .text-holder {
	font-size: 1.5rem;
	line-height: 1.6;
}
.section-mosaic .mosaic-block {
	width: 100%;
	max-width: 28.5rem;
}
.section-mosaic .mosaic-block .h3 {
	margin-bottom: 0.5rem;
	font-family: "Rubik", sans-serif;
	font-weight: 700;
}
.section-mosaic .mosaic-block.blue {
	color: white;
	background: #1b5fc1;
}
.section-mosaic .mosaic-block.white {
	background: white;
}
.section-mosaic .mosaic-block.yellow {
	max-width: 100%;
	padding: 4.5rem 3.8rem 4.9rem 3.8rem;
	color: #000000;
	background: #ffb800;
}
.section-mosaic .block-footer {
	display: flex;
	align-items: center;
}
.section-mosaic .contact-us-holder {
	margin-left: 4rem;
	padding-left: 4rem;
	border-left: solid 0.1rem white;
}
.section-mosaic .contact-us {
	display: inline-flex;
	align-items: center;
	font-weight: 600;
	text-decoration: none;
}
.section-mosaic .contact-us:hover {
	opacity: 0.8;
}
.section-mosaic .contact-us .contact-us-title {
	display: block;
	font-size: 14px;
	line-height: 1.1;
	color: #1b5fc1;
}
.section-mosaic .contact-us .contact-us-number {
	font-size: 2.4rem;
	line-height: 1;
}
.section-mosaic .contact-us .btn-rounded-full {
	width: 4.6rem;
	height: 4.6rem;
	margin-right: 1.3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #1b5fc1;
	border-radius: 50%;
}
.section-mosaic .contact-us .btn-rounded-full img {
	width: 2rem;
}